console.log('Hello from index.js!');

const links = [{
    title: 'Engineering',
    description: 'Betty decided to write a short story and she was sure it was going to be amazing. She had already written it in her head and each time she thought about it she grinned from ear to ear knowing how wonderful it would be. She could imagine the accolades coming in and the praise she would receive for creating such a wonderful piece. She was therefore extremely frustrated when she actually sat down to write the short story and the story that was so beautiful inside her head refused to come out that way on paper.',
    link: 'engineering.html',
    imageClass: 'clark-image',
}, {
    title: 'QUEST',
    description: 'Betty decided to write a short story and she was sure it was going to be amazing. She had already written it in her head and each time she thought about it she grinned from ear to ear knowing how wonderful it would be. She could imagine the accolades coming in and the praise she would receive for creating such a wonderful piece. She was therefore extremely frustrated when she actually sat down to write the short story and the story that was so beautiful inside her head refused to come out that way on paper.',
    link: 'quest.html',
    imageClass: 'quest-image',
},
{
    title: 'Personal',
    description: 'Betty decided to write a short story and she was sure it was going to be amazing. She had already written it in her head and each time she thought about it she grinned from ear to ear knowing how wonderful it would be. She could imagine the accolades coming in and the praise she would receive for creating such a wonderful piece. She was therefore extremely frustrated when she actually sat down to write the short story and the story that was so beautiful inside her head refused to come out that way on paper.',
    link: 'personal.html',
    imageClass: 'personal-image',
}];

const dynamicNav = document.getElementById('dynamic-nav');

const titleRow = document.createElement('tr');
const imageRow = document.createElement('tr');
const descriptionRow = document.createElement('tr');

for (let i = 0; i < links.length; i += 1) {
    const link = links[i];

    const titleData = document.createElement('td');
    const imageData = document.createElement('td');
    const descriptionData = document.createElement('td');

    titleData.setAttribute('class', 'heading');
    titleData.innerHTML = link.title;
    imageData.id = link.imageClass;
    imageData.innerHTML = `<a href="${link.link}"></a>`;
    descriptionData.setAttribute('class', 'text');
    descriptionData.innerHTML = link.description;

    titleRow.appendChild(titleData);
    imageRow.appendChild(imageData);
    descriptionRow.appendChild(descriptionData);
}

dynamicNav.appendChild(titleRow);
dynamicNav.appendChild(imageRow);
dynamicNav.appendChild(descriptionRow);
